	border-radius: 17px;
	background-color: rgba($black, 0.2);
	position: relative;
	transition: all $transition-1;
	cursor: pointer;

	&::before {
		content: "";
		position: absolute;
		top: 50%;
		left: 2px;
		transform: translateY(-50%);
		width: 20px;
		height: 20px;
		border-radius: 50px;
		background-color: $white;
		transition: all $transition-1;
	}
}

.checkbox:checked+.checkbox-style {
	background-color: $white;
}

.checkbox:checked+.checkbox-style::before {
	left: 22px;
}

.checkbox-active {
	display: none;
}

.checkbox-active-style {
	display: block;
	width: 44px;
	height: 22px;
	border-radius: 17px;
	background-color: rgba($black, 0.3);
	position: relative;
	transition: all $transition-1;
