		padding: 45px 20px 20px 20px;
		margin-bottom: 30px;
	}
}

canvas {
	pointer-events: none;
	height: 100%;
	width: 100%;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

#canvas3 {
	z-index: 5;
}

#canvas2 {
	z-index: 10;
}

#canvas1 {
	z-index: 100;
}

.window-weather-days {
	&__item:nth-child(2) {
		.widget-weather-sun {

			&::before,
			&::after {
				display: none;
			}
		}
	}

	&__item:nth-child(3) {
		.widget-weather-sun {

			&::before {
				display: none;
			}
		}
	}


	&__item:nth-child(4) {
		.widget-weather-sun {

			&::after {
				width: 40px;
				height: 26px;
				top: 8px;
				right: -3px;
			}

			&::before {
				display: none;
			}
		}
	}

	&__item:nth-child(5) {
		.widget-weather-sun {

			&::after,
			&::before {
				display: none;
			}
		}
	}

	&__item:nth-child(6) {
		.widget-weather-sun {

			&::after,
			&::before {
				display: none;
			}
		}
	}
}

.window-weather-days__items {
	display: flex;
	align-items: center;
	justify-content: space-around;
}

.window-weather-days__item-title {
	text-align: center;
	@include fz16;
	font-weight: 600;
	color: $white;
	text-shadow: $box-shadow-6;
}

.window-weather-days__item-icon {

	.widget-weather-sun {
		width: 34px;
		height: 34px;

		&::after {
			width: 24px;
			height: 10px;
			bottom: 10px;
			right: -5px;
			animation-name: wCloudMini;
		}

		&::before {
			width: 10px;
			height: 10px;
			bottom: -2px;
			right: 3px;
			animation-name: wDropMini;
		}
	}

	.widget-weather-title {
		@include fz16;
		font-weight: 600;
	}
}

.btn-weather-active {
	background-color: rgba($black, 0.2);
	transition: all $transition-1;

	.widget-weather-title {
		color: $white;
	}
}

.btn-info {
	// animation-name: navBtnRemoveIcon;
	animation-delay: 1.2s;
	animation-duration: 0.4s;
	animation-timing-function: $transition-1-fun;
	animation-fill-mode: forwards;
}

@keyframes navBtnRemoveIcon {
	0% {
		scale: 1;
	}

	80% {
		scale: 0;
	}

	100% {
		display: none;
		scale: 0;
	}
}

.pcactive {
	opacity: 0;
	transition: $transition-1;
}


