/* -display flex */
@mixin dFlex {
	display: flex;
	align-items: center;
}

/* -border-radius */
@mixin bR16 {
	border-radius: $border-radius-24;
}

/* -position */

@mixin posCenterA {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

@mixin posCenterLeft {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
}

@mixin sizeCanvas {
	max-width: 100%;
	width: 100%;
	min-height: 100%;
	height: 100%;
}

/* -btn */
@mixin no-btn {
	width: inherit;
	height: inherit;
	padding: inherit;
	margin: inherit;
	border: inherit;
	background-color: transparent;
	border-radius: 0;
	cursor: pointer;
	-webkit-appearance: none;
}

@mixin btnHover {
	background-color: $white;
	color: $main-color;
	box-shadow: $box-shadow-7;
}

@mixin btnTransition {
	transition: background-color $transition-1, color $transition-1, box-shadow $transition-1;
}

@mixin bgLinerColor {
	background: linear-gradient(118.7deg, $main-color 43.7%, $main-color-2 94.81%);
}

@mixin bgWhite {
	background: radial-gradient(circle, rgba(255, 255, 255, 0.87) 0%, rgba(245, 245, 255, 0.87) 36%, rgba(255, 255, 255, 0.87) 100%);
}

@mixin bgWhite2 {
	background: radial-gradient(circle, rgba(255, 255, 255, 0.4) 0%, rgba(245, 245, 255, 0.4) 36%, rgba(255, 255, 255, 0.4) 100%);
}

@mixin bgWhite3 {
	background: radial-gradient(circle, rgba(255, 255, 255, 0.96) 0%, rgba(245, 245, 255, 0.96) 36%, rgba(255, 255, 255, 0.96) 100%);
}

@mixin bgWhite4 {
	background: radial-gradient(circle, rgba(255, 255, 255, 0.70) 0%, rgba(245, 245, 255, 0.70) 36%, rgba(255, 255, 255, 0.70) 100%);
}

///

@mixin bgBlack {
	background: radial-gradient(circle, rgba(15, 15, 15, 0.8) 0%, rgba(5, 5, 5, 0.8) 36%, rgba(7, 7, 7, 0.8) 100%);
}



@mixin bgBlur {
	backdrop-filter: saturate(3) blur(20px);
}

@mixin bgBlur2 {
	backdrop-filter: blur(3px);
}

@mixin bgBlur3 {
	backdrop-filter: blur(5px);
}

@mixin bgBlur4 {
	backdrop-filter: blur(22px);
}

@mixin bgBlur5 {
	backdrop-filter: blur(8px);
}

@mixin bgBlur6 {
	backdrop-filter: saturate(1) blur(30px);
}

